<template>
    <div class="margin-tb-10 margin-lr-20 layout-content" style="padding-bottom:60px">
        <!--顶部状态步骤栏-->
        <div class="margin-b-10">
            <apply-steps :active-step="orderStep" :first-text="isQy ? '创建订单' :  '预审'"></apply-steps>
        </div>

        <!--订单基本信息栏-->
        <div class="cm-panel">
            <div class="order-info-row flex-top">
                <ul style="flex:1" class="padding-r-20">
                    <li><span class="order-info-label">订单号：</span><span
                            class="order-info-value">{{orderinfo.orderNo}}</span></li>
                    <li v-if=" !isQy || (isQy && !!orderinfo.customerName)"><span class="order-info-label">{{isQy? '企业名称':'个人'}}：</span><span
                            class="order-info-value">{{orderinfo.customerName}}</span>
                    </li>
                    <li v-if="!isQy"><span class="order-info-label">手机号码：</span><span class="order-info-value">{{orderinfo.customerPhone}}</span>
                    </li>
                    <li><span class="order-info-label">产品方案：</span><span class="order-info-value">{{orderinfo.productName}}</span>
                    </li>
                    <li><span class="order-info-label">主体：</span><span
                            class="order-info-value">{{orderinfo.mainSubjectDesc}}</span></li>
                    <li><span class="order-info-label">渠道：</span><span
                            class="order-info-value">{{orderinfo.dealerName}}</span></li>
                    <li><span class="order-info-label">意向车型：</span><span class="order-info-value">{{orderinfo.fullCarStyleText}}</span>
                    </li>
                </ul>
                <div class="order-right-btnbox" v-if="loaded && canToModify">
                    <Button @click="changeProduct" type="primary" ghost>修改产品/车型</Button>
                </div>
            </div>
        </div>

        <!--标签栏-->
        <div id="tabBar" :class="tabBarFixed?'fixed-tab-bar':''">
            <div class="cm-panel">
                <div class="flex-box">
                    <div class="flex-box">
                        <!--:class="$route.name===item.routeName?'active':''"-->
                        <div class="applytab-bar-cell" :class="activeStep===index?'active':''"
                             v-for="(item,index) in orderTabs" :key="index"
                             @click="jump(index)">
                            <!--<Tag type="dot" :color="activeStep===index ? 'blue': ''" @click.native="jump(index)">{{item.title}}</Tag>-->
                            <div class="applytab-bar-cell-title">{{item.title}}</div>
                            <!--进度条-->
                            <div class="ivu-progress ivu-progress-show-info" v-if="index<2">
                                <div class="ivu-progress-outer">
                                    <div class="ivu-progress-inner">
                                        <div class="ivu-progress-bg" style="height: 10px;"
                                             :class="'order-progress-'+getProgressClass(item,index)"
                                             :style1="progressStyle(item,index)"></div>
                                    </div>
                                </div>
                                <span class="ivu-progress-text" :style="progressTextColor(item,index)">{{item.count}}/{{item.total}}                        </span>
                            </div>
                        </div>
                    </div>
                    <div style="flex: 1;" class="text-right font0" v-if="loaded">
                        <Button @click="$router.back()" custom-icon="iconfont icon-back">返回
                        </Button>
                        <Button @click="saveApplyDataFn" type="primary"
                                custom-icon="iconfont icon-save">保存
                        </Button>
                        <!-- <Button @click="submitApply" type="primary" custom-icon="iconfont icon-submit"
                                :disabled="!(orderTabs[0].hasSaved&&orderTabs[1].hasSaved)">提交
                        </Button> -->
                
                        <Button @click="tempHasIcbc" type="primary" custom-icon="iconfont icon-submit" 
                            :disabled="!(orderTabs[0].hasSaved&&orderTabs[1].hasSaved)">提交
                        </Button>
                        <Button @click="openCapitalAdmission" type="primary" custom-icon="iconfont icon-compass"
                                v-if="!isQy">资方准入检测
                        </Button>
                        <!--<Button @click="tableData.repayTbl.showModal=true">还款计划表</Button>-->
                    </div>
                </div>
            </div>
        </div>

        <!--子组件-->
        <div>
            <!-- <router-view></router-view> -->
            <apply-client-module ref="applyClientModule" v-if="loadCustModule && orderData.orderinfo"
                                 v-show="!activeStep"></apply-client-module>

            <apply-loan-module ref="applyLoanModule" v-if="loadLoanModule && orderData.orderinfo"
                               v-show="activeStep==1"></apply-loan-module>


            <div class="cm-panel" v-show="activeStep===2">
                <h3>附件信息</h3>
                <iframe :src="mcloudSrc" ref="mobsf" scrolling="no" frameborder="0" style="width:100%;height:500px;"
                        @load="hideLoading()"></iframe>
            </div>

            <!-- <apply-client-module ref="applyClientModule" v-if="loadCustModule" v-show="!activeStep"></apply-client-module>
      <apply-loan-module ref="applyLoanModule" v-if="loadLoanModule && activeStep===1"></apply-loan-module> -->


        </div>

        <!--弹窗Modal-->
        <Modal v-model="tableData.zifang.showModal" title="资方准入结果" footer-hide width="500">
            <Alert type="error" show-icon v-if="submitZfError" class="margin-b-10" style="margin-top:-10px">
                当前订单未匹配到资方，请修改后再次准入。
            </Alert>
            <div v-else style="margin-top:-10px">
                <div class="color3">根据当前填写的信息，您的订单状态如下：</div>
                <div class="zficon-tip">
                    <span class="zficon-tip-success zf-primary"><span
                            class="iconfont icon-zfsuccess"></span> <span>资方匹配通过</span></span>
                    <span class="zficon-tip-fail zf-error"><span
                            class="iconfont icon-zferror"></span> <span>资方匹配未通过</span></span>
                </div>
            </div>
            <div class="zfresult-tip">
                匹配通过 <span class="zf-primary">{{zfPassNum}}</span> 家，未通过 <span class="zf-error">{{zfRefuseNum}}</span> 家
            </div>
            <div style="max-height:300px;overflow:auto;">
                <ul class="zfresult-list">
                    <li v-for="item in zfResultList">
                        <span>{{item.capitalName}}</span>
                        <span v-html="item.content"></span>
                        <span class="iconfont"
                              :class="item.successFlag?'zf-primary icon-zfsuccess':'zf-error icon-zferror'"></span>
                    </li>
                </ul>
            </div>
        </Modal>
        <!--提交前确认弹窗-->
        <Modal v-model="showSubmitConfirmModal" :mask-closable="false" title="订单信息确认" width="600" footer-hide>
            <div style="font-size: 12px;margin-top:-10px" class="label120">
                <Alert show-icon v-show="this.detailInfo&&this.detailInfo.hasPersonaGuarantor=='1'?true:false">
                    您的订单有担保人，授权短信将发送至担保人手机，请提醒担保人及时征信授权～
                </Alert>
                <div class="info-title-header"><span class="info-title-title">提交备注(选填)</span></div>
                <Row>
                    <i-col span="24">
                        <Input class='remark' :maxlength='300' v-model="remark" type="textarea" :rows="4"
                               placeholder="请输入(字数不超过300字)"></Input>
                    </i-col>
                </Row>
                <div class="info-title-header margin-t-10"><span class="info-title-title">客户信息</span></div>
                <Row>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">订单编号：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.orderNo:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">{{isQy?'企业名称':'客户姓名'}}：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.applyName:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">{{isQy?'统一社会信用代码':'身份证号码'}}：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.certificateNo:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">产品名称：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.productName:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="24">
                        <div class="formview-cell">
                            <span class="formview-label">车型：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.carStyleName:''}}</span>
                        </div>
                    </i-col>
                </Row>
                <!--个人时增加资方信息 start-->
                <div class="info-title-header margin-t-10" v-if="!isQy"><span class="info-title-title">资方准入信息</span>
                </div>
                <Row v-if="!isQy">
                    <i-col span="24">
                        <div class="formview-cell">
                            <span class="formview-label">资方名称：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.fundName:''}}</span>
                        </div>
                    </i-col>
                </Row>
                <!--个人时增加资方信息 end-->
                <div class="info-title-header margin-t-10"><span class="info-title-title">贷款信息</span></div>
                <Row>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">实际销售价(元)：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.actualSellingPrice:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">客户融资额(元)：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.custFinanceAmount:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">融资期限(期)：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.financePeriod:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">首付金额(元)：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.downPaymentAmount:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">首付比例(%)：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.downPaymentProportion:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">尾付金额(元)：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.finalPaymentAmount:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">尾付比例(%)：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.finalPaymentProportion:''}}</span>
                        </div>
                    </i-col>
                    <i-col span="12">
                        <div class="formview-cell">
                            <span class="formview-label">月供(元)：</span>
                            <span class="formview-value">{{this.detailInfo?this.detailInfo.monthlyAmount:''}}</span>
                        </div>
                    </i-col>
                </Row>
                <div class="text-right margin-t-20">
                    <i-button type="primary" :loading="confirmLoading" @click="orderConfirmSave">确认提交</i-button>
                </div>
            </div>
        </Modal>
        <!-- temp工行临时添加 -->
        <Modal 
            v-model="tempModelShow" 
            :mask-closable="false"
            footer-hide
            title="预选准入银行确认"
            width="416">
            <Row>
                <RadioGroup v-model="tempIcbc" size="small">
                    <i-col span="11" push="4"><Radio label="工行优先"></Radio></i-col>
                    <i-col span="11" push="16"><Radio label="不做工行"></Radio></i-col>
                </RadioGroup>
            </Row>
            <Row><br></Row>
            <Row>
                <i-col push="14">
                    <Button 
                        type="default" 
                        @click="tempModelShow = false">取消</Button>
                    <Button type='primary' 
                        @click="tempChooseIcbc">确定</Button>
                </i-col>
            </Row>
        </Modal>

        <!--面包屑-->
        <div class="simple-breadcrumb col-xs-11">
            <router-link to="/order/list">进件管理</router-link>
            <Icon type="ios-arrow-forward"></Icon>
            <span>进件提报</span>
        </div>
    </div>
</template>

<script>
    /**
     * @Description: 订单编辑页
     */
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

    import applySteps from "@/components/apply/applySteps.vue";
    import applyLoanModule from "./loan";
    import applyClientModule from "./cust";

    import eventHub from '@/utils/eventHub';
    import {yx, scrollTo} from "@/utils/helper";

    export default {
        name: "orderEdit",
        components: {
            applySteps,
            applyLoanModule,
            applyClientModule
        },
        data() {
            return {
                orderNo: this.$route.params.orderNo,
                account: this.$store.state.app.userInfo.userAccount,
                userName: this.$store.state.app.userInfo.userName,
                orderStep: 0,//顶部步骤条所在节点，对应12345
                isQy: yx.isQy(this.$route.params.customerType),
                loaded: false, //页面数据是否加载成功
                loadCustModule: false, //加载客户模块
                loadLoanModule: false, //加载贷款模块
                loadAttachModule: false, //加载附件模块
                mcloudSrc: '',
                showSubmitConfirmModal: false, //显示提交前确认弹窗
                activeStep: 0,
                custType: "1",
                showRepayModal: false,
                handleContactorEditIndex: undefined,
                handleBondsmanEditIndex: undefined,
                zfResultArrived: false,//接收到资方准入结果
                zfTimer: undefined,//资方准入请求超时定时器
                tableData: {
                    repayTbl: {
                        showModal: false,
                    },
                    zifang: {
                        title: '资方准入结果',
                        showModal: false,
                        loading: false,
                        total: 0,
                        data: [],
                        columns: [{
                            title: "序号",
                            width: 70,
                            align: 'left',
                            render: (h, params) => {
                                return params.index + 1;
                            }
                        }, {
                            title: '资方名称',
                            key: 'name',
                        },
                            {
                                title: '准入结果',
                                key: 'result',
                                render: (h, params) => {
                                    if (params.row.result == true)
                                        return h('span', {
                                            'class': 'color-primary'
                                        }, '通过');
                                    if (params.row.errors) {
                                        return h('div', params.row.errors.map((item) => {
                                            return h('div', {}, item.index + '、' + item.rule)
                                        }))
                                    }
                                }
                            },
                        ]
                    },
                },
                loading: false,
                exportLoading: false,
                pager: {
                    "total": 0,
                    "Opts": [10, 20, 50, 100, 200]
                },
                dateGroup: [],
                saveT: undefined,
                tempSaving: false,
                tabBarFixed: false,
                zfPassNum: 0,
                zfRefuseNum: 0,
                zfResultList: [],
                detailInfo: {},//详情订单信息
                remark: '',//备注
                submitZfError: false,//错误码AA0018未匹配到资方
                confirmLoading: false, //确认弹窗防止重复提交
                // temp工行临时添加
                tempModelShow: false,
                tempIcbc: "不做工行"
            }
        },
        computed: {
            ...mapState('apply', [
                'orderData',
                'orderTabs'
            ]),
            //禁用保存按钮，确认不要控制置灰，保存按钮为常亮
            saveDisabled() {
                var i = this.activeStep || 0;
                if (i == 2) return false;
                var com = this.orderTabs[i].total > 0 && this.orderTabs[i].total === this.orderTabs[i].count;
                return !com;
            },
            orderinfo() {
                return this.orderData.orderinfo || {};
            },
            //可以跳转预审页修改产品/车型时才显示按钮
            canToModify() {
                return this.orderinfo.productModifyStatus == '1' || this.orderinfo.carModifyStatus == '1' || this.orderinfo.carModifyStatus == '2';
            },
            //因进度条组件无法同时绑定进度条和字体颜色 ，所以改成直接使用元素渲染
            //获取进度条百分比及背景色
            progressStyle() {
                return (item, index) => {
                    return {
                        'width': this.getProgressPct(item, index) + '%',
                        'background-color': this.getProgressClass(item, index) == 'success' ? '#1FA269' :
                            this.getProgressClass(item, index) == 'wrong' ? '#F5222D' : '#999999'
                    }
                }
            },
            //进度条颜色控制
            progressTextColor() {
                return (item, index) => {
                    return {
                        color: this.getProgressClass(item, index) == 'success' ? '#1FA269' :
                            this.getProgressClass(item, index) == 'wrong' ? '#F5222D' : '#999999'
                    }
                }
            }

        },
        mounted() {
            document.title = '进件提报';
            //初始查询进件信息
            this.queryInit();
            //监听页面滚动事件
            window.addEventListener('scroll', this.handleScroll);
        },
        //离开当前页面后执行
        destroyed() {
            window.removeEventListener('scroll', this.handleScroll); //页面销毁时取消页面滚动监听事件
            if (window.saveT) clearInterval(window.saveT)
            eventHub.$off('VENUS_PIECE_DISCOVER');
            sessionStorage.removeItem('cust'); //页面销毁时清除cust缓存
            sessionStorage.removeItem('loan'); //页面销毁时清除loan缓存
        },
        methods: {
            ...mapMutations('apply', [
                'setOrderInfo', 'setOrderModuleError', 'setOrderCustTabSaved', 'setOrderLoanTabSaved'
            ]),
            ...mapMutations('app', [
                'showLoading',
                'hideLoading',
                'showZfLoading',
                'hideZfLoading',
            ]),
            //封装报错弹窗
            errorModal(content, onOk, okText) {
                var arr = content.split(';');
                var text = '';
                arr.forEach(item => {
                    if (item) text += !!text ? ('；<br>' + item) : item;
                })
                let options = {
                    title: '提示',
                    okText: okText || '确 定',
                    content: text,
                };
                if (onOk) options.onOk = onOk;
                this.$Modal.error(options);
            },
            //进度条的样式
            getProgressClass(item, index) {
                let fill = item.count > 0;//只要填写了就有颜色
                return item.hasError ? 'wrong' : (fill ? 'success' : 'normal');
            },

            //进度条的百分比
            getProgressPct(item, index) {
                return (item.count * 100) / item.total;
            },
            //标签页吸顶
            handleScroll() {
                var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
                var offsetTop = document.querySelector('#tabBar').offsetTop;
                this.tabBarFixed = scrollTop > offsetTop;
            },
            //初始化数据查询
            async queryInit() {
                let that = this;
                //订单状态
                let order = await this.queryOrderStatusInfo();
                order.orderNo = this.orderNo;
                const steps = ['level11', 'level12', 'level13', 'level14'];
                let stepIndex = steps.indexOf(order.orderSubStage);
                this.orderStep = stepIndex > -1 ? stepIndex + 1 : 1;//设置顶部节点
                this.setOrderInfo(order);
                this.loadCustModule = true;
                //加载贷款模块
                this.loadLoanModule = true;
                //数据请求结束
                this.loaded = true;
                //订单保存状态{attachment: 0, apply: 0, finance: 0}
                let saved = await this.queryOrderSaveStatus();
                this.setOrderCustTabSaved(saved.apply === 1)
                this.setOrderLoanTabSaved(saved.finance === 1)
            },
            //查询订单状态节点及预审等基本信息
            async queryOrderStatusInfo() {
                let url = polarisserver + '/credit/getOrderModifyDetail/' + this.account + '/' + this.orderNo;
                this.showLoading();
                return new Promise((resolve, reject) => {
                    this.$http.get(url).then((res) => {
                        this.hideLoading();
                        let result = res.data;
                        if (!result.success) return this.errorModal(result.errorMessage || '订单状态请求失败');
                        if (!result.data) return this.errorModal('没有该订单状态信息');
                        resolve(result.data);
                    }).catch((err) => {
                        this.hideLoading();
                        this.errorCatchModal('订单状态请求出错：', err)
                    })
                })
            },
            //查询订单模块保存状态
            async queryOrderSaveStatus() {
                let url = polarisserver + '/order/module/save/status/' + this.orderNo;
                this.showLoading();
                return new Promise((resolve, reject) => {
                    this.$http.get(url).then((res) => {
                        this.hideLoading();
                        let result = res.data;
                        if (!result.success) return this.errorModal(result.errorMessage || '订单保存状态请求失败');
                        if (!result.data) return this.errorModal('没有该订单保存状态信息');
                        resolve(result.data);
                    }).catch((err) => {
                        this.hideLoading();
                        this.errorCatchModal('订单保存状态请求出错：', err)
                    })
                })
            },
            //请求云盘页面地址
            queryMcoudSrc() {
                this.showLoading();
                let params = {
                    "orderId": this.orderNo,
                    "viewCode": "yixin_xiaofei_tibao_polaris",
                    "orderUserId": this.account,
                    "orderUserName": this.userName
                }
                this.$http.post(baseserver + '/annexStorage/annexcloudview', params).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) {
                        return this.errorModal(result.errorMessage || '云盘页面地址请求失败');
                    }
                    this.mcloudSrc = result.data.visitURL;
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('云盘页面地址请求出错：', err);
                })
            },
            //请求必传但未上传的附件列表
            async queryAttachValidate() {
                let url = baseserver + '/cloudStorage/annexordervalidate';
                this.showLoading();
                return new Promise((resolve, reject) => {
                    this.$http.post(url, {orderId: this.orderNo}).then((res) => {
                        this.hideLoading();
                        let result = res.data;
                        if (!result.success || !result.data) return this.errorModal(result.errorMessage || '附件上传状态请求失败');
                        var items = result.data.items;
                        if (result.data.orderValidate) {
                            resolve(true)
                        } else if (!!items) {
                            var txt = '<h4>必传附件未上传：</h4>';
                            for (let p in items) {
                                txt += items[p] + '；<br>'
                            }
                            this.errorModal(txt);
                            resolve(false);
                        } else {
                            this.errorModal('附件不完整但是未返回未上传的附件列表');
                        }
                    }).catch((err) => {
                        this.hideLoading();
                        this.errorCatchModal('附件上传状态请求出错：', err)
                    })
                })
            },
            //点击保存按钮，保存，校验
            async saveApplyDataFn() {
                if (this.activeStep === 0) {
                    let clientModule = this.$refs.applyClientModule;
                    let custSave = await clientModule.toSave();
                    return custSave;
                }
                else if (this.activeStep === 1) {
                    let loanModule = this.$refs.applyLoanModule;
                    let loanSave = await loanModule.toSave();
                    return loanSave;
                }
                else if (this.activeStep === 2) {
                    //请求必传但未上传的附件列表
                    let attachSave = await this.queryAttachValidate();
                    return attachSave;
                }
            },
            //确认提交和提交时如果返回错误码AA0018时代表未匹配到资方，调用新接口
            queryZfFailRule() {
                this.showLoading();
                var url = polarisserver + '/venus/failRule'
                this.$http.post(url, {orderNo: this.orderNo}).then(res => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success || !result.data) return this.errorModal(result.errorMessage || '未匹配资方信息请求失败');
                    //回填未匹配资方信息
                    this.openZfResultModal(result.data.matchResultDetailList, true);
                }).catch(err => {
                    this.hideLoading();
                    this.errorCatchModal('未匹配资方信息请求出错：', err);
                })
            },
            // temp 选择工行
            tempChooseIcbc(){
                this.tempModelShow = false
                this.submitApply()
            },
            // temp 检测是否
            tempHasIcbc(){
                this.showLoading()
                this.tempIcbc = '不做工行'
                this.$http.post(polarisserver+'/order/exist/icbc', {orderNo: this.orderNo}).then(res=>{
                    this.hideLoading()
                    let result = res.data;
                    if(result.data && result.success){
                        this.tempModelShow = true
                    }else{
                        this.submitApply()
                    }
                }).catch(err => {
                    this.hideLoading();
                    this.errorCatchModal('工行验证请求出错：', err);
                })
            },

            //提交全部数据，先请求订单信息确认信息，显示订单信息确认弹框
            async submitApply() {
                this.showLoading();
                this.remark = '';
                let priorityIcbc= this.tempIcbc==='不做工行'?0:1
                this.$http.post(polarisserver + '/order/confirm', {orderNo: this.orderNo, priorityIcbc:priorityIcbc}).then(res => {
                    this.hideLoading();
                    let result = res.data;
                    if (result.errorCode == 'AA0018' && !!result.data) {
                        //确认提交和提交时如果返回错误码AA0018时代表未匹配到资方，有数据时回填未匹配资方信息
                        this.openZfResultModal(result.data, true);
                        return;
                    }
                    if (!result.success) return this.errorModal(result.errorMessage || '订单信息确认请求失败');
                    this.showSubmitConfirmModal = true;
                    this.detailInfo = result.data;
                }).catch(err => {
                    this.hideLoading();
                    this.errorCatchModal('订单信息确认请求出错：', err);
                })
            },
            //确认弹窗中的 按钮  订单确认提交
            orderConfirmSave() {
                this.confirmLoading = true;
                this.showSubmitConfirmModal = false;
                this.showLoading();
                let obj = {
                    orderNo: this.orderNo,
                    remark: this.remark,
                    saveFrom: 'PC'
                };
                this.$http.post(polarisserver + '/order/submit', obj).then(res => {
                    this.hideLoading();
                    this.confirmLoading = false;
                    let result = res.data;
                    if (result.errorCode == 'AA0018' && !!result.data) {
                        //确认提交和提交时如果返回错误码AA0018时代表未匹配到资方，有数据时回填未匹配资方信息
                        this.openZfResultModal(result.data, true);
                        return;
                    }
                    if (!result.success) return this.errorModal(result.errorMessage || '订单提交请求失败');
                    this.$Modal.success({
                        title: '提 示',
                        content: '提交成功!',
                        onOk: () => {
                            this.$router.replace({
                                name: "orderList"
                            });
                        }
                    });
                }).catch(err => {
                    this.hideLoading();
                    this.confirmLoading = false;
                    this.errorCatchModal('订单提交请求出错：', err);
                })
            },
            go(routename, params) {
                this.$router.push({
                    name: routename,
                    params: params
                })
            },
            handleReset(tblName) {
                let formName = this.tableData[tblName].formName
                this.$refs[formName].resetFields();
            },
            //获取所有保存信息，资方准入用
            getFullData() {
                let loanModule = this.$refs.applyLoanModule;
                let clientModule = this.$refs.applyClientModule;
                var loanData = loanModule.getSaveData()
                var clientData = clientModule.getSaveData()
                return {
                    isApp: false,
                    account: this.account,
                    "orderNo": this.orderNo,
                    "personal": clientData.personal,
                    "enterprise": clientData.enterprise,
                    "contactInfoList": clientData.contactInfoList,
                    "relationship": clientData.relative,
                    "personalGuarantor": clientData.personalGuarantor,
                    "enterpriseGuarantor": clientData.enterpriseGuarantor,
                    "financeBaseInfoDTO": loanData.financeBaseInfo,
                    "financeItemInfoDTO": loanData.financeItemInfo,
                    "financeDiscountInfoDTO": loanData.financeDiscountInfo,
                    "financeMarginInfoDTO": loanData.financeMarginInfo,
                    "financeCalculationInfoDTO": loanData.financeCalculationInfo,
                }
            },
            //清除资方准入结果的数据
            clearZfResult() {
                this.zfPassNum = 0;
                this.zfRefuseNum = 0;
                this.zfResultList = [];
                this.zfResultArrived = false;
            },
            //回填资方准入结果并显示弹窗
            openZfResultModal(zfList, isZfError) {
                zfList = zfList || [];
                this.zfResultList.splice(0);//清空资方表格内容
                this.tableData.zifang.showModal = true;
                this.submitZfError = isZfError;//是否确认或提交时的错误码
                var listA = [];
                var listB = [];
                zfList.forEach((item) => {
                    var content = '';
                    if (item.successFlag) {
                        content = '通过';
                    } else if (!!item.matchFailRecordList && item.matchFailRecordList.length > 0) {
                        item.matchFailRecordList.forEach((regs) => {
                            content += regs.ruleMessage + '<br/>';
                        })
                    } else {
                        content = '未获取失败规则';
                    }
                    item.content = content.replace(/undefined/g, "").replace(/<br\/>$/g, "");
                    if (item.successFlag) {
                        listA.push(item)
                    } else (
                        listB.push(item)
                    )
                })
                this.zfPassNum = listA.length;
                this.zfRefuseNum = listB.length;
                this.zfResultList = listA.concat(listB);
                this.zfResultArrived = true;

            },
            //处理websocket接收到的消息，已转换后的消息body
            acceptMessage(data) {
                if (data && data.messageType == 'VENUS_PIECE_DISCOVER'
                    && data.extendValue && data.extendValue.applyNo == this.orderNo) {
                    eventHub.$off('VENUS_PIECE_DISCOVER');
                    this.hideZfLoading();
                    var zfList = data.extendValue.messageBody.adaptCapitalList;//准入结果
                    this.openZfResultModal(zfList);

                }
            },
            //资方准入检测接口
            openCapitalAdmission() {
                this.clearZfResult();
                let fullData = this.getFullData();
                this.showZfLoading();
                //建立socket连接，全局监听
                Socklink.link((linked) => {
                    if (!linked) {
                        this.hideZfLoading();
                        return this.errorModal('资方准入连接请求失败，请重试！');
                    }
                    //监听消息回传
                    eventHub.$off('VENUS_PIECE_DISCOVER');
                    eventHub.$on('VENUS_PIECE_DISCOVER', (response) => {
                        this.acceptMessage(response);//已转换后的消息body
                    })
                    this.$http.post(polarisserver + '/venus/discover', fullData).then(res => {
                        let result = res.data;
                        if (!result.success) {
                            eventHub.$off('VENUS_PIECE_DISCOVER');
                            this.hideZfLoading();
                            return this.errorModal(result.errorMessage || '资方准入请求失败，请稍候再试')
                        }
                        let zfTimer = setTimeout(() => {
                            /*本地测试用 this.$http.get(polarisserver+'/venus/zfresult').then(aa=>{
                            this.hideZfLoading();
                                var zfList = aa.data.extendValue.messageBody.adaptCapitalList;//准入结果
                                this.openZfResultModal(zfList);
                            })*/
                            if (!this.zfResultArrived) {
                                this.hideZfLoading();
                                this.errorModal('资方准入请求超时，请稍候再试');
                            }
                            clearTimeout(zfTimer);
                        }, 20000)
                    }).catch(err => {
                        eventHub.$off('VENUS_PIECE_DISCOVER');
                        this.hideZfLoading();
                        this.errorCatchModal('资方准入请求出错：', err)
                    })
                })
            },
            //修改产品车型信息，跳转预审页
            changeProduct() {
                this.$Modal.confirm({
                    title: '提醒',
                    content: '是否确认修改产品车型信息？',
                    okText: '去修改',
                    cancelText: '放弃修改',
                    onOk: () => {
                        this.$router.push({
                            name: 'orderPreaudit',
                            params: {
                                customerType: this.$route.params.customerType,
                            },
                            query: {
                                change: '1',
                                orderNo: this.orderinfo.orderNo
                            }
                        })
                    },
                })
            },
            jump(index) {
                if (!this.orderData.orderinfo) return false;
                this.activeStep = index;
                //滚定到位置tabBar
                let el = $('#tabBar')[0];
                if (el) {
                    var preEl = $('#tabBar').prev();
                    var topEl = preEl.prev();
                    var elsH = preEl.outerHeight(true) + topEl.outerHeight(true) + 10;
                    var barH = $('#tabBar').outerHeight();
                    let st = document.documentElement.scrollTop || document.body.scrollTop;
                    //滚定到位置，固定时滚到顶部减掉固定的bar高，未固定时滚到之前位置
                    let total = this.tabBarFixed ? elsH - barH : st;
                    scrollTo(document, total, 300);
                }
                if (index === 1) {
                    //加载贷款模块
                    this.loadLoanModule = true;
                }

                if (index === 2 && !this.mcloudSrc) {
                    this.queryMcoudSrc();
                }
                /*if (this.activeStep == 2 && index != 2) {
                    this.$Modal.confirm({
                        title: '提示',
                        content: '请确认附件信息已保存时再离开！',
                        okText: '确 认',
                        cancelText: '去保存',
                        onOk: () => {
                            this.activeStep = index;
                        },
                    })
                } else {
                    this.activeStep = index;
                    if (index === 2 && !this.mcloudSrc) {
                        this.queryMcoudSrc();
                    }
                }*/
            }
        }
    }
</script>



